'use client';

import { useEffect, useState } from 'react';
import axios from 'axios';
import ProtectedRoute from '../../../components/ProtectedRoute';

const PayrollReportsPage = () => {
  const [reports, setReports] = useState([]);

  useEffect(() => {
    const fetchReports = async () => {
      const res = await axios.get(`${process.env.NEXT_PUBLIC_API_URL}/payroll/generate-all`, {
  withCredentials: true
});
      setReports(res.data);
    };
    fetchReports();
  }, []);

  return (
    // <ProtectedRoute role="admin">
      <div className="p-6">
        <h2 className="text-xl font-semibold mb-4">Payroll Reports</h2>
        <table className="min-w-full bg-white">
          <thead>
            <tr>
              <th className="py-2">Employee</th>
              <th className="py-2">Month</th>
              <th className="py-2">Present</th>
              <th className="py-2">Leaves</th>
              <th className="py-2">Absent</th>
              <th className="py-2">Total Salary</th>
            </tr>
          </thead>
          <tbody>
            {reports.map((r) => (
              <tr key={r._id}>
                <td className="text-center py-1">{r.name}</td>
                <td className="text-center py-1">{r.month}</td>
                <td className="text-center py-1">{r.presentDays}</td>
                <td className="text-center py-1">{r.leaveDays}</td>
                <td className="text-center py-1">{r.absentDays}</td>
                <td className="text-center py-1">${r.totalSalary}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    // </ProtectedRoute>
  );
};

export default PayrollReportsPage;
